@mixin container {
  @media (max-width: ($maxWidthContainer)) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet2 {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile2 {
  @media (max-width: (480px)) {
    @content;
  }
}
