html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: $gothamProFont;
  background-color: var(--color-white);
}

body.lock {
  overflow: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
  max-width: $maxWidthContainer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.js-validate-error-label {
  color: #f20909 !important;
}

.js-validate-error-field {
  color: var(--color-black) !important;
  border: none !important;
}

@keyframes show_header {
  0% {
      opacity: 0;
      top: -100px;
  }
  100% {
      opacity: 1;
      top: 0;
  }
}